\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n\n  },\n  mounted () {\n    this.init()\n    this.erAuth()\n  },\n\n\n}\n</script>\n<style scoped>\n.search {\n  width: 20%;\n  margin: 0 0 10px 20px;\n}\n.head_pic {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n  border-radius: 50%;\n}\n</style>"],"sourceRoot":""}]);

